













import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component
export default class Datechoose extends Vue {
  @Prop(String) DateChoose!: string;
  @Prop(String) title?: string;
  show = false;
  minDate = new Date(2020, 0, 1);
  maxDate = new Date();
  formatDate(date: any) {
    return `${date.getYear() + 1900}-${date.getMonth() + 1}-${date.getDate()}`;
  }
  onConfirm(date: any) {
    this.show = false;
    this.$emit("update:DateChoose", this.formatDate(date));
  }
}
