



































































import Types from "@/components/Money/Types.vue";
import Layout from "@/components/Layout.vue";
import Vue from "vue";
import { Component } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import intervalList from "@/constants/intervalList";
import dayjs from "dayjs";
import clone from "@/lib/clone";
import Datechoose from "@/components/Datechoose.vue";
import Makemoney from "@/components/Makemoney.vue";
import { Dialog } from "vant";
import Ecb from "@/components/Ecb.vue";

@Component({
  components: { Types, Layout, Tabs, Datechoose, Makemoney, Ecb },
})
export default class Statistics extends Vue {
  show = false;
  detailedDate = [];
  showPopup(item: any) {
    item.createdAt = dayjs(item.createdAt).format("YYYY/M/D");
    this.detailedDate = item;
    this.show = true;
  }
  checked = false;
  onInput(checked: boolean) {
    Dialog.confirm({
      title: "温馨提醒",
      message: "是否查看支出收入统计？",
    }).then(() => {
      this.checked = checked;
    });
  }

  ShowMake = true;
  ShowEcharts = false;
  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(new Date(), "day")) {
      return "今天";
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return "昨天";
    } else if (day.isSame(new Date(), "year")) {
      return day.format("M月D日");
    } else {
      return day.format("YYYY年M月D日");
    }
  }
  get recordList() {
    return (this.$store.state as RootState).recordList;
  }
  get groupedList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      this.ShowMake = false;
      this.ShowEcharts = true;
      return [];
    }
    if (
      clone(recordList).filter((r) => r.type === "-").length === 0 ||
      clone(recordList).filter((r) => r.type === "+").length === 0
    ) {
      this.ShowEcharts = true;
    }
    const newList = clone(recordList)
      .filter((r) => r.type === this.type)
      .sort(
        (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
      )
      .filter(
        (x) =>
          dayjs(x.createdAt).valueOf() <
          dayjs(this.ThisDate + " 23:59:59").valueOf()
      );
    if (newList.length === 0) {
      this.ShowMake = false;
      return [];
    } else {
      this.ShowMake = true;
    }
    type Result = {
      title: string | undefined;
      items: RecordItem[];
      total?: number;
    }[];
    const result: Result = [
      {
        title: newList[0].createdAt,
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
        last.items.push(current);
      } else {
        result.push({
          title: current.createdAt,
          items: [current],
        });
      }
    }
    result.forEach((group) => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return result;
  }
  beforeCreate() {
    this.$store.commit("fetchRecords");
  }
  type = "-";
  // interval = "day";
  ThisDate = dayjs(new Date()).format("YYYY/M/D");
  intervalList = intervalList;
}
