<template>
  <div id="b2"></div>
</template>

<script>
import dayjs from "dayjs";
export default {
  name: "b2",
  methods: {
    getData(result, xData) {
      let data = [];
      for (let i = 0; i < xData.length; i++) {
        data.push(0);
      }
      for (let i = 0; i < xData.length; i++) {
        for (let x = 0; x < result.length; x++) {
          if (dayjs(xData[i]).isSame(result[x].title, "day")) {
            data.splice(i, 1, result[x].total);
          }
        }
      }
      return data;
    },
    getGroup(xList, result) {
      for (let i = 1; i < xList.length; i++) {
        const current = xList[i];
        const last = result[result.length - 1];
        if (dayjs(last.title).isSame(dayjs(current.createdAt), "day")) {
          last.items.push(current);
        } else {
          result.push({
            title: current.createdAt,
            items: [current],
          });
        }
      }
      result.forEach((group) => {
        group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
      });
      return result;
    },
    drawChart() {
      const recordList = this.$store.state.recordList;
      const ExpenditureList = recordList
        .filter((r) => r.type === "-")
        .sort(
          (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
        );
      const RevenueList = recordList
        .filter((r) => r.type === "+")
        .sort(
          (a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf()
        );
      let result1 = [
        {
          title: ExpenditureList[0].createdAt,
          items: [ExpenditureList[0]],
          total: 0,
        },
      ];
      let result2 = [
        {
          title: RevenueList[0].createdAt,
          items: [RevenueList[0]],
          total: 0,
        },
      ];
      result1 = this.getGroup(ExpenditureList, result1);
      result2 = this.getGroup(RevenueList, result2);
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("b2"));
      // 指定图表的配置项和数据
      let xData = (function () {
        let data = [];
        const day = dayjs(new Date()).daysInMonth();
        const month = (dayjs(new Date()).month() + 1).toString();
        const year = dayjs(new Date()).year().toString();
        for (let i = 1; i <= day; i++) {
          data.push(year + "/" + month + "/" + i);
        }
        return data;
      })();
      let ExpenditureData = this.getData(result1, xData);
      let RevenueData = this.getData(result2, xData);

      let option = {
        backgroundColor: "#1A1835",
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
            textStyle: {
              color: "#fff",
            },
          },
        },
        grid: {
          borderWidth: 0,
          top: 110,
          bottom: 95,
          x: 50,
          y: 55,
          textStyle: {
            color: "#fff",
          },
        },
        legend: {
          x: "46%",
          top: "11%",
          textStyle: {
            color: "#90979c",
          },
          data: ["支出", "收入"],
        },

        calculable: true,
        xAxis: [
          {
            type: "category",
            axisLine: {
              lineStyle: {
                color: "rgba(204,187,225,0.5)",
              },
            },
            splitLine: {
              show: false,
            },
            axisTick: {
              show: false,
            },
            data: xData,
          },
        ],

        yAxis: [
          {
            type: "value",
            splitLine: {
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "rgba(204,187,225,0.5)",
              },
            },
          },
        ],
        dataZoom: [
          {
            show: true,
            height: 30,
            xAxisIndex: [0],
            bottom: 30,

            start: 0,
            end: 100,
            handleIcon:
              "path://M306.1,413c0,2.2-1.8,4-4,4h-59.8c-2.2,0-4-1.8-4-4V200.8c0-2.2,1.8-4,4-4h59.8c2.2,0,4,1.8,4,4V413z",
            handleSize: "110%",
            handleStyle: {
              color: "#5B3AAE",
            },
            textStyle: {
              color: "rgba(204,187,225,0.5)",
            },
            fillerColor: "rgba(67,55,160,0.4)",
            borderColor: "rgba(204,187,225,0.5)",
          },
          {
            type: "inside",
            show: true,
            height: 15,
            start: 1,
            end: 35,
          },
        ],
        series: [
          {
            name: "支出",
            type: "line",
            symbolSize: 10,
            symbol: "circle",
            itemStyle: {
              color: "#6f7de3",
            },
            markPoint: {
              label: {
                normal: {
                  textStyle: {
                    color: "#fff",
                  },
                },
              },
              data: [
                {
                  type: "max",
                  name: "最大值",
                },
                {
                  type: "min",
                  name: "最小值",
                },
              ],
            },
            data: ExpenditureData,
          },
          {
            name: "收入",
            type: "line",
            symbolSize: 10,
            symbol: "circle",
            itemStyle: {
              color: "#c257F6",
            },
            markPoint: {
              label: {
                normal: {
                  textStyle: {
                    color: "#fff",
                  },
                },
              },
              data: [
                {
                  type: "max",
                  name: "最大值",
                },
                {
                  type: "min",
                  name: "最小值",
                },
              ],
            },
            data: RevenueData,
          },
        ],
      };

      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
      window.onresize = function () {
        myChart.resize();
      };
    },
  },

  mounted() {
    this.drawChart();
  },
};
</script>

<style lang="scss" scoped>
#b2 {
  height: 450px;
}
#b2,
html,
body {
  width: 100%;
}
</style>